<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="items"
      item-key="name"
      :search="search"
    >
        <template v-slot:top>
            <v-text-field
                v-model="search"
                label="Search"
                class="mx-4"
            ></v-text-field>
            <v-form v-on:submit.prevent>
                <v-text-field
                    v-on:keyup.enter="nameFilter"
                    v-model="backend_search"
                    label="Search Name(backend)"
                    class="mx-4"
                ></v-text-field>
            </v-form>
        </template>
    </v-data-table>
    
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    data () {
      return {
        search: '',
        backend_search: '',
        items: [],
      }
    },
    mounted () {
        axios
        .get('http://localhost:8000/bhavcopy_data')
        .then(response => {
            console.log(response.data)
            this.items = response.data
        })
    },
    computed: {
      headers () {
        return [
          { text: 'CODE', value: 'SC_CODE',},
          { text: 'NAME',value: 'SC_NAME',},
          { text: 'OPEN', value: 'OPEN' },
          { text: 'LOW', value: 'LOW' },
          { text: 'HIGH', value: 'HIGH' },
          { text: 'CLOSE', value: 'CLOSE' },
        ]
      },
    },
    methods: {
        nameFilter () {
            axios
            .get('http://localhost:8000/search', {
                params: {name: this.backend_search}
            })
            .then(response => { this.items = response.data})
            .catch(error => console.log(error))
        },
    },
  }
</script>